<template>
  <div
    v-loading="isLoading"
    class="metadata-management-table-info relative"
  >
    <div class="text-right">
      <gs-button
        v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED'"
        :style="{visibility: !isEdit ? 'visible' : 'hidden'}"
        @click="edit(true)"
      ></gs-button>
    </div>
    <gs-form
      ref="form"
      label-width="95px"
    >
      <h3>
        <i
          :style="{transform: packUp1 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp1=!packUp1"
        />
        <span></span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp1">
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.showTableName }}</span>
          </gs-form-item>
          <!-- <gs-form-item
            label=""
          >
            <span>{{ tableInfo.business }}</span>
          </gs-form-item> -->
          <gs-form-item
            label=""
          >
            <div
              v-for="(relation, index) in tableInfo.relations"
              :key="index"
            >{{ relation }}</div>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.owner }}</span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.createTime }}</span>
          </gs-form-item>
          <!-- <gs-form-item
            label=""
          >
            <span>{{ tableInfo.category }}</span>
          </gs-form-item> -->
          <gs-form-item
            label=""
          >
            <i
              class="iconfont icon-yuanshuju"
            />
            <span>{{ tableInfo.databaseName }}</span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.type }}</span>
          </gs-form-item>
          <!-- <gs-form-item
            label=""
          >
            <span>{{ tableInfo.tableLife }}</span>
          </gs-form-item> -->
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.partitionTable ? '' : '' }}</span>
          </gs-form-item>
          <!-- <gs-form-item
            label=""
          >
            <span>{{ tableInfo.partitionLife }}</span>
          </gs-form-item> -->
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.format }}</span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.location }}</span>
          </gs-form-item>
          <gs-form-item
            ref="description"
            label=""
          >
            <span v-if="!isEdit">{{ tableInfo.description }}</span>
            <gs-input
              v-else
              v-model="description"
              placeholder=""
            />
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <!-- <h3>
        <i
          :style="{transform: packUp2 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp2=!packUp2"
        />
        <span></span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp2">
          <gs-form-item
            label=""
            prop="database"
          >
            <span>{{ tableInfo.topic }}</span>
          </gs-form-item>
        </div>
      </gs-collapse-transition> -->
      <h3>
        <i
          :style="{transform: packUp3 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp3=!packUp3"
        />
        <span></span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp3">
          <gs-form-item
            label=""
            prop="database"
          >
            <gs-checkbox
              v-model="canRead"
              :disabled="true"
              class="table-permission"
            ></gs-checkbox>
            <gs-checkbox
              v-model="canWrite"
              :disabled="true"
              class="table-permission"
            ></gs-checkbox>
          </gs-form-item>
        </div>
      </gs-collapse-transition>
    </gs-form>
    <div
      v-show="isEdit"
      class="action-btns"
    >
      <action-comfirm
        title=""
        @comfirm="updateTableInfo"
      >
        <gs-button
          slot="reference"
          type="primary"
        >
        </gs-button>
      </action-comfirm>
      <gs-button
        @click="edit(false)"
      ></gs-button>
    </div>
  </div>
</template>

<script>
import './table-info.scss';
import * as tableInfoService from 'src/service/metadata-management/table-info';
import ActionComfirm from 'src/components/action-comfirm';
export default {
  components: {
    ActionComfirm
  },
  props: {
    tableInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      isLoading: false,
      isEdit: false,
      packUp1: false,
      // packUp2: false,
      packUp3: false,
      description: '',
      canWrite: false,
      canRead: false
    };
  },
  watch: {
    tableInfo: {
      handler: function (val) {
        this.canRead = val.tablePermission ? val.tablePermission.read : false;
        this.canWrite = val.tablePermission ? val.tablePermission.write : false;
      },
      immediate: true
    }
  },
  methods: {
    edit(flag) {
      this.isEdit = flag;
      if (flag) {
        this.description = this.tableInfo.description;
        this.$refs['description'].$el.scrollIntoView();
      }
    },
    updateTableInfo() {
      if (this.description === this.tableInfo.description) {
        this.$Message.warning('');
        return;
      }
      this.isLoading = true;
      tableInfoService.updateTableInfo({
        guid: this.$route.params.id,
        description: this.description
      }).then((res) => {
        this.isLoading = false;
        this.edit(false);
        // 
        this.tableInfo.description = this.description;
        // this.$emit('refreshTableInfo');
        this.$Message.success('');
      }).catch((res) => {
        this.isLoading = false;
      });
    }
  }
};
</script>




